@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  cursor: url(/terminal-project/static/media/General-Cursor.5bc19059.svg), auto;
}
@media (min-width: 0) {
  body {
    height: 100vh;
    overflow: hidden;
    width: 100vw;
  }
}

html {
  height: 100vh;
  width: 100vw;
}

@media (min-width: 0) {
  .App-Wrapper {
    height: 100%;
    width: 100%;
  }
}

.fadeIn {
  -webkit-animation: fadeIn 2s;
  animation: fadeIn 2s;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadeOut {
  -webkit-animation: fadeOut 0.5s forwards;
  animation: fadeOut 0.5s forwards;
}

@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@media (min-width: 0) {
  .First-Wrapper {
    background: #0d0d0d;
    height: 100%;
    left: 0;
    position: relative;
    top: 0;
    width: 100%;
  }
}

@media (min-width: 0) {
  .First-Texture {
    height: 100%;
    left: 0;
    opacity: 5%;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 3;
  }
}

@media (min-width: 0) {
  .First-Text {
    align-items: center;
    color: #F23E2E;
    display: flex;
    font-size: 2em;
    font-family: "Lato", sans-serif;
    font-weight: 900;
    height: 100vh;
    justify-content: center;
    position: absolute;
    width: 100vw;
    z-index: 2;
  }
}
@media (min-width: 769px) {
  .First-Text {
    font-size: 3em;
  }
}
@media (min-width: 1100px) {
  .First-Text {
    font-size: 3.5em;
  }
}

@media (min-width: 0) {
  .First-Background {
    background: url(/terminal-project/static/media/First-Background.7dbfd7b0.jpg);
    background-size: cover;
    height: 100vh;
    opacity: 60%;
    width: 100vw;
  }
}

.Second-Wrapper {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

@media (min-width: 0) {
  .Second-ButtonDiv {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
  }
}

@media (min-width: 0) {
  .Second-ButtonImage {
    height: 8vh;
    min-width: 110px;
    min-height: 40px;
    opacity: 80%;
    position: absolute;
    transition: 0.2s ease-out;
    bottom: 10vh;
    width: 11vw;
    z-index: 4;
  }
}
.Second-ButtonImage:hover {
  height: 9vh;
  opacity: 100%;
  transition: 0.2s ease-in;
  width: 12vw;
}

@media (min-width: 0) {
  .Second-Texture {
    height: 100vh;
    width: 100vw;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 3;
    opacity: 10%;
  }
}

@media (min-width: 0) {
  .Second-Eagle {
    height: auto;
    left: calc(100vw/2 - 25px);
    max-width: 50px;
    max-height: 130px;
    position: absolute;
    top: 20vh;
    width: auto;
    z-index: 2;
  }
}
@media (min-width: 769px) {
  .Second-Eagle {
    left: calc(100vw/2 - 37.5px);
    max-width: 75px;
    max-height: 175px;
  }
}

@media (min-width: 0) {
  .Second-Background {
    background: url(/terminal-project/static/media/Second-BackgroundSmall.953510dc.jpg) repeat-x;
    background-size: cover;
    height: 100vh;
    width: 200vw;
  }
}
@media (min-width: 650px) {
  .Second-Background {
    background: url(/terminal-project/static/media/Second-BackgroundMedium.0676e35e.jpg) repeat-x;
    background-size: cover;
  }
}
@media (min-width: 769px) {
  .Second-Background {
    background: url(/terminal-project/static/media/Second-BackgroundLarge.9d931868.jpg) repeat-x;
    background-size: cover;
  }
}

.Second-Background {
  -webkit-animation: slideBackground 60s linear infinite;
          animation: slideBackground 60s linear infinite;
  background-position: 0% 0%;
}

@-webkit-keyframes slideBackground {
  from {
    background-position: 0% 0%;
  }
  to {
    background-position: -200vw 0%;
  }
}
@keyframes slideBackground {
  from {
    background-position: 0% 0%;
  }
  to {
    background-position: -200vw 0%;
  }
}

